.buttons{margin:0 auto;width:100%;display:inline}.buttons a{margin:10px;-webkit-box-shadow:0 1px 4px rgba(0,0,0,.3),0 0 40px rgba(0,0,0,.1) inset;box-shadow:0 1px 4px rgba(0,0,0,.3),inset 0 0 40px rgba(0,0,0,.1);-webkit-transition:all .45s cubic-bezier(.23,1,.32,1) 0ms;-o-transition:all .45s cubic-bezier(.23,1,.32,1) 0ms;transition:all .45s cubic-bezier(.23,1,.32,1) 0ms}.buttons a:link,.buttons a:visited{background-color:#fff;color:#000;padding:10px 20px;text-align:center;text-decoration:none;display:inline-block;border-radius:2px}.buttons a:active,.buttons a:hover{background-color:rgba(0,0,0,.078);-webkit-box-shadow:0 10px 6px -6px rgba(0,0,0,.3),0 0 40px rgba(0,0,0,.1) inset;box-shadow:0 10px 6px -6px rgba(0,0,0,.3),inset 0 0 40px rgba(0,0,0,.1)}.secondaryButton a:link,.secondaryButton a:visited{background-color:#00bcd4;color:#fff}.secondaryButton a:active,.secondaryButton a:hover{background-color:#00a1b7}.defaultButton a:active,.defaultButton a:hover,.secondaryButton a:active,.secondaryButton a:hover{-webkit-box-shadow:0 10px 6px -6px rgba(0,0,0,.3),0 0 40px rgba(0,0,0,.1) inset;box-shadow:0 10px 6px -6px rgba(0,0,0,.3),inset 0 0 40px rgba(0,0,0,.1)}.defaultButton a:active,.defaultButton a:hover{background-color:rgba(0,0,0,.078)}.defaultButtonSelected{background-color:rgba(0,0,0,.2)!important;color:#fff!important}.secondaryButtonSelected{background-color:#008ba1!important}